<script lang="ts">

    // import event dispatcher
    import { createEventDispatcher, onMount } from 'svelte';

    export let x: string;
    export let y: string;
    export let scale: number = 1;

    let candle: HTMLElement;
    let flame: HTMLElement;

    // create event dispatcher
    const dispatch = createEventDispatcher();

    onMount(() => {
        // set top and left position
        candle.style.top = `${y}px`;
        candle.style.left = `${x}px`;

        console.debug("Candle mounted");
    });

    let burning: boolean = true;

    function onBlowCandle() {

        // remove event listener
        candle.removeEventListener('click', onBlowCandle);
        flame.removeEventListener('click', onBlowCandle);

        burning = false;
        // dispatch blowed event
        dispatch('blowed');
    }

</script>

<template>
    <!-- svelte-ignore a11y-click-events-have-key-events -->
    <div bind:this={candle} class="wrapper" on:click={onBlowCandle}>
        <div class="candle"></div>
        <div class="flame" bind:this={flame} on:click={onBlowCandle} class:off={!burning} ></div>
    </div>
</template>

<style>

    .wrapper {
        position: absolute;
    }

    /* candle is a div with a width of 10px and a height of 100px */
    .candle {
        width: 10px;
        height: 100px;
        background-color: antiquewhite;
        border-radius: 5px;
    }

    /* flame is a div that is located at the bottom of the candle element */
    .flame {
        width: 20px;
        height: 20px;
        background-color: red;
        border-radius: 50%;
        position: relative;
        top: -20px;
        left: -5px;
        transition: opacity 0.5s;
        z-index: 999;
    }

    .off {
        opacity: 0;
    }

</style>