<script lang="ts">

// simple beerpong game with 6 cups where the player can flick balls into the cups	
// rendered to canvas

import { onMount } from 'svelte';
import { createEventDispatcher } from 'svelte';

// create dispatcher
const dispatch = createEventDispatcher();

let canvas: HTMLCanvasElement;
let ctx: CanvasRenderingContext2D;
let gameHeight: number = 600;
let gameWidth: number = 400;
let manager: GameObjectManager;

const debug = false;

let blueCup = false;

let siteOpened = false;

let targetPlane: TargetPlane;
let ball: Ball;
let score: number = 0;
let gameEnd = false;

export function siteNowOpen() {
    siteOpened = true;
}

onMount(() => {
    manager = new GameObjectManager();
    // get the canvas context
    ctx = canvas.getContext('2d');

    targetPlane = new TargetPlane(0, 0, gameWidth, gameHeight - 200);
    ball = new Ball();
    // create six cups in inverted pyramid centered
    manager.add(new Cup(100, 100));
    manager.add(new Cup(200, 100));
    manager.add(new Cup(300, 100));
    manager.add(new Cup(150, 150));
    manager.add(new Cup(250, 150));
    manager.add(new Cup(200, 200));
    manager.add(targetPlane);
    manager.add(ball);

    manager.loop();
});

interface GameObject {
    x: number;
    y: number;
    width: number;
    height: number;
    draw(ctx: CanvasRenderingContext2D): void;
    update(): void;
}

class Sprite {
    image: HTMLImageElement;
    width: number;
    height: number;
    constructor(url: string, width: number, height: number) {
        // create image
        this.image = new Image();
        this.image.src = url;

        this.width = width;
        this.height = height;
    }
    draw(ctx: CanvasRenderingContext2D, x: number, y: number) {
        ctx.drawImage(this.image, x, y, this.width, this.height);
    }
}

class GameObjectManager {
    objects: GameObject[];
    constructor() {
        this.objects = [];
    }
    add(object: GameObject) {
        this.objects.push(object);
    }

    isGameEnd() {
        let activeCount = 0;
        this.getCups().forEach(cup => {
            if (cup.getHitBox().active) {
                activeCount++;
            }
        });

        if (activeCount === 0) {
            return true;
        }

    }
    

    get(index: number) {
        return this.objects[index];
    }

    getLength() {
        return this.objects.length;
    }

    getCups() {
        return this.objects.filter(object => object instanceof Cup);
    }

    forEach(callback: (object: GameObject) => void) {
        this.objects.forEach(callback);
    }

    update() {
        this.objects.forEach(object => {
            object.update();
        });
    }
    draw(ctx: CanvasRenderingContext2D) {
        // clear the canvas
        ctx.clearRect(0, 0, gameWidth, gameHeight);
        this.objects.forEach(object => {
            object.draw(ctx);
        });

        // draw score in the lower left corner
        ctx.font = '40px Amatic SC';
        ctx.fillStyle = '#333';
        ctx.fillText('Score: ' + score, 10, gameHeight - 10);

        // draw message "Yay, You Are Awesome!" when game is over centered on screen
        if (gameEnd) {
            ctx.font = '50px Amatic SC';
            ctx.fillStyle = '#333';
            ctx.fillText('Yay, You Are Awesome!', gameWidth / 2 - 150, gameHeight / 2);
        }

    }
    loop() {
        this.update();
        this.draw(ctx);
        requestAnimationFrame(() => this.loop());
    }
}

class TargetPlane implements GameObject {
    x: number;
    y: number;
    width: number;
    height: number;
    projectedPoint: { x: number, y: number };
    sourcePoint: { x: number, y: number };

    constructor(x: number, y: number, width: number, height: number) {
        this.x = x;
        this.y = y;
        this.width = width;
        this.height = height;
        this.sourcePoint = { x: this.x + this.width / 2, y: y + this.height };

    }
    draw(ctx: CanvasRenderingContext2D) {
        if (!debug) return;
        // cyan 50% opacity
        ctx.fillStyle = 'rgba(0, 255, 255, 0.2)';
        ctx.fillRect(this.x, this.y, this.width, this.height);
        // draw projected point if it exists
        if (this.projectedPoint) {
            ctx.fillStyle = 'red';
            ctx.fillRect(this.projectedPoint.x, this.projectedPoint.y, 25, 25);
        }

        // draw source point as circle around it
        ctx.beginPath();
        ctx.arc(this.sourcePoint.x, this.sourcePoint.y, 10, 0, 2 * Math.PI);
        ctx.stroke();


    }
    update() {
        // do nothing
    }

    // project angle and length to plane position 
    // returns point on plane
    // projection based on the sourcePoint
    // clamp values so its always inside of the plane
    project(angle: number, length: number) {
        // convert angle to radians
        angle = angle * Math.PI / 180;
        // calculate x and y point one is sourcePoint and we have the angle and length and need the end point
        let x = this.sourcePoint.x + length * Math.cos(angle);
        let y = this.sourcePoint.y + length * Math.sin(angle);

        // set projected point
        this.projectedPoint = { x: x, y: y };

        return this.projectedPoint;

    }



}

class Ball implements GameObject {
  x: number;
  y: number;
  width: number;
  height: number;
  hitBox: HitBox;
  sprite: Sprite;
    
  animCos: number = 0;

  checkingCollision: boolean = false;

    sourcePosition: { x: number, y: number };
    targetPosition: { x: number, y: number };
    isThrown: boolean;

    constructor() {
        this.isThrown = false;
        this.x = 0;
        this.y = 0;
        this.width = 30;
        this.height = 30;
        this.hitBox = new HitBox(0, 0, 30, 30);
        // source position is on the bottom center of the canvas
        this.sourcePosition = { x: gameWidth / 2, y: gameHeight - 50 };
        this.resetToSourcePos();
        this.sprite = new Sprite('/images/ball.png', 40, 40);
        // create hitbox
        this.hitBox = new HitBox(this.x, this.y, this.width, this.height);

    }

    setTargetPosition(x: number, y: number) {
        if (this.isThrown) {
            return;
        }
        this.targetPosition = { x: x, y: y };
        this.isThrown = true;
    }

    resetToSourcePos() {
        this.x = this.sourcePosition.x;
        this.y = this.sourcePosition.y;
        this.isThrown = false;
    }

  draw(ctx: CanvasRenderingContext2D): void {
    // draw ball sprite
    this.sprite.draw(ctx, this.x -30, this.y -30);
    // ctx.beginPath();
    // ctx.arc(this.x, this.y, 25, 0, 2 * Math.PI);
    // ctx.fillStyle = 'red';
    // ctx.fill();
    // ctx.stroke();

    this.hitBox.drawDebug(ctx);


    // draw hitbox
    // this.hitBox.drawDebug(ctx);

  }
  update(): void {
    // when thrown, move in 0.75 seconds to target position and stay for 0.5 seconds on the target position
    if (this.isThrown) {
        // calculate distance to target
        let distance = Math.sqrt(Math.pow(this.targetPosition.x - this.x, 2) + Math.pow(this.targetPosition.y - this.y, 2));
        // calculate direction to target
        let direction = { x: (this.targetPosition.x - this.x) / distance, y: (this.targetPosition.y - this.y) / distance };
        // move ball in direction of target
        this.x += direction.x * 10;
        this.y += direction.y * 10;

        // update hitbox
        this.hitBox.x = this.x - 25;
        this.hitBox.y = this.y - 25;

        // check if ball is close enough to target
        if (distance < 10) {
            this.x = this.targetPosition.x;
            this.y = this.targetPosition.y;
            // bouncing ball animation
            this.y -= 1;
            setTimeout(() => this.y += 1, 100);
            setTimeout(() => this.y -= 1, 200);
            setTimeout(() => this.y += 1, 300);
            setTimeout(() => this.y -= 1, 400);
            // reset ball to source position
                
            if (!this.checkingCollision) {
                this.checkingCollision = true;
                setTimeout(() => {
                    let cups = manager.getCups();
                    // check if ball collided with cup hit box
                    for (let i = 0; i < cups.length; i++) {
                        if (cups[i].getHitBox().collides(this.hitBox)) {
                            // if so, reset ball to source position
                            cups[i].getHitBox().setActive(false);
                            cups[i].hit();
                            this.checkingCollision = false;
                            score += 25

                            if (manager.isGameEnd()) {
                                gameEnd=true;
                                dispatch('win');
                                setTimeout(() => {
                                    // alert('Game Over');
                                    manager.getCups().forEach(cup => cup.reset());
                                    gameEnd=false;
                                    blueCup = !blueCup;
                                }, 2500);
                            }

                            this.resetToSourcePos();
                            return;
                        }
                    }
                    this.checkingCollision = false;
                    score -= 5;
                    this.resetToSourcePos()
                }, 500);
            }
        }
    } else {
        this.animCos += 0.05;
        if (this.animCos > 1) {
            this.animCos = -1;
        }

        this.y = this.sourcePosition.y + Math.cos(this.animCos) * 60 - 30;

    }

  }

}

class Cup implements GameObject {
    x: number;
    y: number;
    width: number;
    height: number;
    hitBox: HitBox;
    sprite: Sprite;
    spriteBlue: Sprite;

    opacity: number = 1;

    cosCount = 0;
    disapear: boolean;
    wasHit: boolean;

    reset() {
        this.disapear = false;
        this.wasHit = false;
        this.opacity = 1;
        this.cosCount = 0;
        this.hitBox.active = true;
    }

    hit() {
        this.wasHit = true;
        setTimeout(() => this.disapear = true, 750);
        this.cosCount = 0;
    }

    getHitBox() {
        return this.hitBox;
    }

    constructor(x: number, y: number) {
        this.sprite = new Sprite('/images/cup.png', 80, 105.521472393);
        this.spriteBlue = new Sprite('/images/cup_blue.png', 80, 105.521472393);
        this.x = x;
        this.y = y;
        this.hitBox = new HitBox(x -35, y -45, 70, 40);
        this.wasHit = false;
        this.disapear = false;
    }

    draw(ctx: CanvasRenderingContext2D): void {

        // bounce animation when wasHit
        if (this.wasHit) {
            this.y -= Math.cos(this.cosCount * Math.PI);
            // increase cos count to one and then reverse
            this.cosCount += 0.1;
            if (this.cosCount > 1) {
                this.cosCount = 1;
                this.wasHit = false;
            }
        }

        // when disapear is true, decrease opacity
        if (this.disapear) {
            this.opacity -= 0.05;
            if (this.opacity < 0) {
                this.opacity = 0;
            }
        }
        
        ctx.save();
        // set sprite opacity
        ctx.globalAlpha = this.opacity;
        
        // render sprite
        if (blueCup) {
            this.spriteBlue.draw(ctx, this.x - 40, this.y - 40);
        } else {
            this.sprite.draw(ctx, this.x - 40, this.y - 40);
        }
        // this.sprite.draw(ctx, this.x - 40, this.y - 40);
        ctx.restore();
        // render hitbox
        this.hitBox.drawDebug(ctx);
    }
    update(): void {
        // throw new Error('Method not implemented.');
    }
}

class HitBox {
    x: number;
    y: number;
    width: number;
    height: number;
    active: boolean;

    constructor(x: number, y: number, width: number, height: number) {
        this.x = x;
        this.y = y;
        this.width = width;
        this.height = height;
        this.active = true;
    }

    setActive(active: boolean) {
        this.active = active;
    }

    collides(hitArea: HitBox) {
        if (this.active === false) return false;
        return this.x < hitArea.x + hitArea.width &&
            this.x + this.width > hitArea.x &&
            this.y < hitArea.y + hitArea.height &&
            this.y + this.height > hitArea.y;
    }

    drawDebug(ctx: CanvasRenderingContext2D) {
        if (!debug) return;
        // when inactive user other color
        if (this.active === false) {
            // blue 50% opacity
            ctx.strokeStyle = 'rgba(0, 0, 255, 0.5)';
            ctx.fillStyle = 'rgba(0, 0, 255, 0.5)';
        } else {
            // magenta 50% opacity
            ctx.strokeStyle = 'rgba(255, 0, 255, 0.5)';
            ctx.fillStyle = 'rgba(255, 0, 255, 0.5)';
        }
        ctx.strokeRect(this.x, this.y, this.width, this.height);
        // fill rect
        ctx.fillRect(this.x, this.y, this.width, this.height);
    }

}

export function onSwipe(event: CustomEvent) {

    var pos = targetPlane.project(event.detail.angle, event.detail.length);

    ball.setTargetPosition(pos.x, pos.y);


}
</script>

<template>
    <div class="game-wrapper">
        <div class="game">
            <canvas bind:this={canvas} width={gameWidth} height={gameHeight}></canvas>
        </div>
    </div>
</template>

<style>
        .game-wrapper {
        width: 18rem;
        height: 26rem;
        /* center content flexbox */
        display: flex;
        justify-content: center;
        align-items: center;

    }

    .game {
        height: 24rem;
    }

    canvas {
        /* border: 1px solid black; */
        height: 100%;
    }
</style>
