<script lang="ts">
    
    import { onMount } from "svelte";
    import Candle from "./Candle.svelte";

    let candleWrapper: HTMLElement;

    let amountOfCandles: number;

    onMount(() => {
        amountOfCandles = candleWrapper.children.length;
        console.debug("BirthdayCake mounted");
    });

    function onBlowCandle() {
        console.count("blow");
        amountOfCandles--;
        checkReady();
    }

    function checkReady() {
        if (amountOfCandles == 0) {
            alert("Happy Birthday!");
        }
    }

</script>

<template>
    <div class="candle-wrapper" bind:this={candleWrapper}>
        <Candle x=20 y=5 on:blowed={onBlowCandle}></Candle>
        <Candle x=40 y=5 on:blowed={onBlowCandle}></Candle>
        <Candle x=60 y=5 on:blowed={onBlowCandle}></Candle>
        <Candle x=80 y=5 on:blowed={onBlowCandle}></Candle>
    </div>
</template>

<style scoped>

    .candle-wrapper {
        position: relative;
        width: 100%;
        height: 100%;
    }

</style>