
export enum SwipeDirection {
    None,
    Left,
    Right,
    Up,
    Down
}

export const swipeThreshold = 50;

export class Cup {

    public row: string;
    public column: string;
    public id: string;
    public active: boolean = true;

    public constructor(row: string, column: string, id: string) {
        this.row = row;
        this.column = column;
        this.id = id;
        this.active = true;
    }

    public match(row: string, column: string) : boolean {
        return this.row === row && this.column === column;
    }

    public isActive() : boolean {
        return this.active;
    }

    public setInactive() {
        this.active = false;
    }

}

export class CupManager {

    public cups: Cup[];

    public constructor() {
        this.cups = [];
    }

    public addCup(cup: Cup) {
        this.cups.push(cup);
    }

    public getCup(row: string, column: string) : Cup {
        return this.cups.find(cup => cup.match(row, column));
    }

    public getCupByIndex(index: number) : Cup {
        if (index < 0 || index >= this.cups.length) {
            return null;
        }
        return this.cups[index];
    }

    public getMatchingCup(row: string, column: string) : Cup {
        
        let cup: Cup;

        this.cups.forEach(c => {

            if (!c.isActive()) {
                return;
            }

            if (c.match(row, column)) {
                cup = c;
            }
        });

        return cup;

    }

}


export class Swipe {

    static getDirection(start: Position, end: Position): SwipeDirection {
        const dx = end.x - start.x;
        const dy = end.y - start.y;
        const adx = Math.abs(dx);
        const ady = Math.abs(dy);

        if (adx < swipeThreshold && ady < swipeThreshold) {
            return SwipeDirection.None;
        }

        if (adx > ady) {
            if (dx > 0) {
                return SwipeDirection.Right;
            } else {
                return SwipeDirection.Left;
            }
        } else {
            if (dy > 0) {
                return SwipeDirection.Down;
            } else {
                return SwipeDirection.Up;
            }
        }
    }

    static getAngle(start: Position, end: Position): number {
        const dx = end.x - start.x;
        const dy = end.y - start.y;
        return Math.atan2(dy, dx) * 180 / Math.PI ;
    }

    static getLength(start: Position, end: Position): number {
        const dx = end.x - start.x;
        const dy = end.y - start.y;
        return Math.sqrt(dx * dx + dy * dy);
    }

}

// function that checks if given number is in the range of the other two numbers
export function inRange(value: number, min: number, max: number) {
    return value >= min && value <= max;
}

export class RangeCheck {

    public number: number;

    constructor(number) {
        this.number = number;
    }

    public in(min: number, max: number) {
        return inRange(this.number, min, max);
    }

}

export class Position {
   
    public x: number;
    public y: number;

    public constructor(x: number, y: number) {
        this.x = x;
        this.y = y;
    }

}