<script lang="ts">

  // get event dispatcher

  import { createEventDispatcher, onMount } from 'svelte';


  const dispatch = createEventDispatcher();


  let page: any;



  let open: boolean = false;
  let closed: boolean = false;
  let contentLoad: boolean = false;

  let cardBack: any;
  let cardFront: any;

  export let cover: boolean = false;

  // rotate the page to an specified angle, anchored on the left side of the div
  export function openPage(angle, time) {
    page.style.transform = `rotateY(${angle}deg)`;
    page.style.transition = `transform ${time}s`;
  }

  export function setZIndex(zIndex) {
    page.style.zIndex = zIndex;
  }


  export function next() {
    closed = false;
    open = true;
    dispatch('opened');

    setTimeout(() => {
      // cardBack.style.zIndex = 5;
      // cardFront.style.zIndex = cardBack.style.zIndex - 1;
    }, 1000 * (1.5 / 2));
    console.log("next");
    // setZIndex(-50);
  }

  export function prev() {
    open = false;
    closed = true;
    setTimeout(() => {
      // cardFront.style.zIndex = 5;
      // cardBack.style.zIndex = cardFront.style.zIndex - 1;
    }, 1000 * (1.5 / 2));
    console.log("prev");
    // setZIndex(50);
  }

  export function setCover() {
    cover = true;
  }

  export function setContentVisible() {
    contentLoad = true;
  }

  export function isOpen(): boolean {
    return open;
  }
</script>

<template>
  <div
    bind:this={page}
    class:cover
    class:page={!cover}
    class="flip-card"
    class:contentLoaded={!contentLoad}
  >
    <div class="flip-card-inner" class:open class:closed>
      <div bind:this={cardBack} class="flip-card-back" />
      <div bind:this={cardFront} class="flip-card-front">
        <slot />
      </div>
    </div>
  </div>
</template>

<style scoped>
  .flip-card {
    position: absolute;
    top: 0px;
    left: 0px;
    background-color: transparent;

    perspective: 1000px;
    transform-origin: left center;
  }

  .cover {
    height: var(--card-height);
    width: var(--card-width);
  }

  .page {
    height: var(--page-height);
    width: var(--page-width);
  }

  .page > .flip-card-inner {
    background-color: var(--page-background-color);
    margin-top: calc((var(--card-height) - var(--page-height)) / 2);
  }

  .page > .flip-card-inner > .flip-card-back {
    background-color: var(--page-background-color);
  }

  .cover > .flip-card-inner > .flip-card-front {
    background-color: var(--card-background-color);
  }

  .cover > .flip-card-inner > .flip-card-back {
    background-color: var(--card-background-color);
  }

  .flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.6s;
    transform-origin: left center;
    transform-style: preserve-3d;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
  }

  /* .flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
} */

  .flip-card-front,
  .flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    /* -webkit-backface-visibility: hidden; */
    /* backface-visibility: hidden; */
  }

  .flip-card-front {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    /* transform-origin: left center; */
    color: black;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
  }

  .flip-card-back {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    /* background-color: var(--card-background-color); */
    top: 0px;
    left: 0px;
    /* transform-origin: center center; */
    color: white;
    transform: rotateY(180deg);
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
  }

  .page-wrapper {
    display: block;
    height: var(--page-height);
    width: var(--page-width);
    /* background-color: var(--page-background-color); */
    margin-top: calc((var(--card-height) - var(--page-height)) / 2);
    position: absolute;
    pointer-events: none;
    transition: transform 1.5s;
    transform-origin: left center;
    perspective: 145rem;

    transform-style: preserve-3d;

    /* margin-top: 0px; */
    /* margin-left: 20rem; */
  }

  .front-background,
  .back-background {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    position: absolute;
    height: 100%;
    width: 100%;
  }

  .front-background {
    background-color: var(--page-background-color);
    pointer-events: none;
    transition: transform 1.5s;
    transform-origin: left center;
    perspective: 145rem;
    transform-style: preserve-3d;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    background-color: blue;
  }

  .back-background {
    background-color: var(--page-background-color);
    pointer-events: none;
    transition: transform 1.5s;
    transform-origin: left center;
    perspective: 145rem;
    transform-style: preserve-3d;
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
    background-color: black;
  }

  /* .page-wrapper:after {
        content: '';
        right: 0px;
        bottom: 0px;
        position: absolute;
        top: 0px;
        left: 0px;
        background-color: var(--page-background-color);
        -webkit-transform: rotateY( 180deg );
        transform: rotateY( 180deg );
        -webkit-transform-style: preserve-3d;
        transform-style: preserve-3d;
        -webkit-backface-visibility: hidden;
        backface-visibility: hidden;
    } */

  .page-wrapper-cover {
    display: block;
    height: var(--card-height);
    width: var(--card-width);
    position: absolute;
    pointer-events: none;
    transition: transform 1.5s;
    transform-origin: left center;
    perspective: 145rem;
    transform-style: preserve-3d;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
  }

  /* .page-wrapper-cover:after {
        content: '';
        right: 0px;
        bottom: 0px;
        position: absolute;
        top: 0px;
        left: 0px;
        background-color: var(--card-background-color);
        -webkit-transform: rotateY( 180deg );
        transform: rotateY( 180deg );
        -webkit-transform-style: preserve-3d;
        transform-style: preserve-3d;
        -webkit-backface-visibility: hidden;
        backface-visibility: hidden;
    } */

  /* .page-background {
        height: var(--page-height);
        width: var(--page-width);
        background-color: var(--page-background-color);
        margin-top: calc((var(--card-height) - var(--page-height)) / 2);
        position: absolute;
        pointer-events: none;
        transition: 1s;
        transform-origin: center left;  
    } */

  .open {
    /* opacity: 0;
         */
    /* transform: rotateY(180deg); rotate3d(0, 1, 0, -110deg); */
    /* rotate animatin*/
    animation: rotating 1.5s linear;
    animation-fill-mode: forwards;

    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  }

  .closed {
    animation: rotateClose 1.5s linear;
    animation-fill-mode: forwards;
  }

  .contentLoaded {
    display: none;
  }

  @keyframes rotating {
    0% {
      transform: rotateY(0deg);
    }
    50% {
      transform: rotateY(-90deg);
    }
    100% {
      transform: rotateY(-150deg);
    }
  }

  @keyframes rotateClose {
    0% {
      transform: rotateY(-150deg);
    }
    50% {
      transform: rotateY(-90deg);
    }
    100% {
      transform: rotateY(0deg);
    }
  }
  .rotating {
    -webkit-animation: rotating 2s linear infinite;
    -moz-animation: rotating 2s linear infinite;
    -ms-animation: rotating 2s linear infinite;
    -o-animation: rotating 2s linear infinite;
    animation: rotating 2s linear infinite;
  }
</style>
