<script lang="ts">
        import Page from "../Page.svelte";
import Bp from "../Games/BP.svelte";
import { Confetti } from "svelte-confetti"

    let conf:any;

    let page: Page;
    let beerPong: Bp;

    export function getPage(){
        return page;
    }

    export function getBeerPong() {
        return beerPong;
    }

    function onWin() {
        new Confetti({
            target: conf,
            props: {
                amount:150, 
                duration: 2000,
                size:15,
                x:[-0.8, 0.8],
                y:[0.25, 2],

                colorArray:[
				'#FDDB77',
				'#A9C874',
				'#63AC7F',
				'#308C82',
				'#266A76',
				'#2F4858'
			]
            }
        });                   

    }

</script>

<template>
    <Page  bind:this={page}>
        <Bp on:win={onWin} bind:this={beerPong}></Bp>
        <!-- <BeerPong bind:this={beerPong}></BeerPong> -->
    	<div bind:this={conf} class="conf">

	</div>
    </Page>
</template>


<style scoped>
    .conf {
        position: absolute;
        width: 100%;
		display: flex;
		justify-content: center;
        bottom: 0;
        width: 100%;
        height: 1px;
        z-index: 100;
    }
    </style>