<script lang="ts">
    import { onMount } from 'svelte';
    import type Page from './Page.svelte';
    import { Confetti } from "svelte-confetti"
    import type FlappyBirdPage from './Pages/FlappyBirdPage.svelte';

    let card: any;
    export let pages: Page[] = [];
    let openPages: Page[] = [];
    let activePage: Page;
    let mounted: boolean = false;
    let activeIndex: number = 0;
    let flappyBird: FlappyBirdPage;

    let changeAllowed = true;

    onMount(() => {
        console.debug("App mounted");

        // get svelte child instances from slot
        pages = [...card.children];

        mounted = true;
    });

    export function nextPage(){
        if (!changeAllowed) return;

        if (activeIndex < pages.length - 1) {
            changeAllowed = false;

            // rotate page to the left
            activePage.getPage().next();
            activeIndex++;
            activePage = pages[activeIndex];
            activePage.getPage().setContentVisible();
            setTimeout(() => {
                openPages.push(activePage);
                console.log(openPages)
    
                sortPages();
            changeAllowed = true;

            }, 750);

        }
        sortOpenPagesZIndex();

    }

 // order z-index of open pages


    export function prevPage() {
        if (!changeAllowed) return;
        
        if (activeIndex > 0) {
            changeAllowed = false;
            // rotate page to the right
            activeIndex--;
            activePage = pages[activeIndex];
            activePage.getPage().prev();
            activePage.getPage().setContentVisible();
            setTimeout(() => {
                openPages.pop();
                sortPages();
                console.log(openPages)
                changeAllowed = true;
            }, 750);
        }
    }

    export function create(cls) {
        pages.push(new cls({target: card}));
        pages.forEach((page, index) => {
            page.getPage().setZIndex(pages.length - index);
        });
        activePage = pages[activeIndex];
        // activePage.getPage().setContentVisible();
        return this;
    }

    export function ready() {
        activePage = pages[activeIndex];
        activePage.getPage().setContentVisible();
        activePage.getPage().setCover();
        sortPages();

        flappyBird = getPage('FlappyBirdPage');

        return this;
    }

    export function getPage(name) {

        let page: any;

        pages.forEach((p) => {
            if (p.constructor.name === name) {
                page = p;
            }
        });

        return page;

        // return pages.find(page => page.constructor.name === name)[0];
    }

    export function getBeerPong(){
        return this.getPage('BeerPongPage').getBeerPong();
    }

    function sortOpenPagesZIndex() {
        return;
        openPages.forEach((page, index) => {
            page.getPage().setZIndex(pages.length - index);
        });
    }

    function sortPages() {
        openPages.forEach((page, index) => {
            page.getPage().setZIndex(pages.length - index);
        });


        // sort closed pages
        pages.forEach((page, index) => {
            if (!openPages.includes(page)) {
                page.getPage().setZIndex(pages.length - index);
            }
        });

    }

    export function onTap() {

    }

</script>

<template>
    <div class="card-container">
        <div bind:this={card} class="card" class:card-tadaa={mounted}>
            <slot /> 
        </div>
    </div>
</template>

<style>

    .card-container {
        height: 100%;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        /* gradient background */
        background-color: #fefefe; /* fallback for old browsers */ 
        background: -webkit-linear-gradient(to bottom, #fefefe, rgb(227, 227, 227)); /* Chrome 10-25, Safari 5.1-6 */
        background: linear-gradient(to bottom, #fefefe, rgb(227, 227, 227)); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

    }

    .card {
        position: relative;
        background-color: var(--card-background-color);
        border-top-right-radius: 8px;
        border-bottom-right-radius: 8px;
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
        width: var(--card-width);
        height: var(--card-height);
    }

    .card-tadaa {
        animation: tada; /* referring directly to the animation's @keyframe declaration */
        animation-duration: 2s; /* don't forget to set a duration! */
    }

</style>